.popup__close-button {
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
  background: none;
  background-image: url(../../../images/close.svg);
  background-repeat: no-repeat;
  background-size: cover;
  border: none;
  position: absolute;
  top: -40px;
  right: -40px;
  transition: opacity 0.2s linear;
}

.popup__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}

@media (max-width: 600px) {
  .popup__close-button {
    width: 20px;
    height: 20px;
    top: -36px;
    right: 0px;
  }
}
